<script>
import RoundedRect from "../primitives/RoundedRect.svelte"

let status = 'component loaded'
let isModelDetailVisible = true

AFRAME.registerComponent('model-click', {
    init: function(){
        const el = this.el

        el.addEventListener('model-loaded', function(){
            status = 'model loaded'
        })

        if(status === 'model loaded'){
            el.addEventListener('click', this.handleClick)
        }
    },
    handleClick: function(ev){
        ev.stopPropagation();
        ev.preventDefault();
		//const method = ev.target.getAttribute('id')
        const el = ev.detail.intersection && ev.detail.intersection.object.el;

        if (el && el === ev.target) {
            // do something
            console.log('click')
        }
    }
})

const modelExt = "obj"
const objModelUrl = "obj: url(./static/models/chair/jean_obj.obj); mtl: url(./static/models/chair/jean_obj.mtl)"
const gltfModelUrl="url(/path/to/tree.gltf)";

const titleBlock = {
	position:{
		x: -0.5,
		y: 0 ,
		z: -1,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height:0.2,
		width: 1	
	},
	borderRadius: {
		topLeft: 0.05,
		topRight:0.05,
		bottomLeft:0.05,
		bottomRight:0.05
	},
	color: "#1B1B1B"
}

const titleText = {
	text: "Single Sofa",
	padding: 0,
	position:{
		x: titleBlock.position.x,
		y: titleBlock.position.y + 0.05,
		z: titleBlock.position.z - titleBlock.size.height/2,
	},
	rotation: {
		x: -90,
		y: 0,
		z: 0,
	},
	size:{
		height: titleBlock.size.height ,
		width: titleBlock.size.width - 0.2
	},
	font: "https://cdn.aframe.io/fonts/Roboto-msdf.json",
	color: "white",
	wrapCount: "20", //controls how big the text is
	xOffset: 0.1,
	zOffset: 0.001,
	height: 1,
	align: 'center',
	baseline: 'center',
	letterSpacing: 2,
	anchor: 'left'

}

</script>

<svelte:head>

</svelte:head>

<RoundedRect params={titleBlock} />
<a-text value={titleText.text} position={`${titleText.position.x} ${titleText.position.y} ${titleText.position.z}`} rotation={`${titleText.rotation.x} ${titleText.rotation.y} ${titleText.rotation.z}`} width={titleText.size.width} height={titleText.size.height} color={titleText.color} wrap-count={titleText.wrapCount} z-offset={titleText.zOffset} x-offset={titleText.xOffset} letter-spacing={titleText.letterSpacing} font={titleText.font} align={titleText.align} anchor={titleText.anchor} ></a-text>

{#if modelExt === "obj"}
    <a-text value="Model Loading" visible={status === "component loaded"} rotation="-90 0 0" width='1' height="1" positon="-0.5 0 0"></a-text>
    <a-entity model-click obj-model={objModelUrl} rotation="-90 0 0"></a-entity>
{:else if modelExt === "gltf" || modelExt === "glb"}
    <a-text value="Model Loading" visible={status === "component loaded"} rotation="-90 0 0" width='1' height="1" positon="-0.5 0 0"></a-text>
    <a-entity  model-click gltf-model={gltfModelUrl} scale="1 1 1" rotation="-90 0 0"></a-entity>
{/if}

<img id="rotate-icon" src="./static/icons/rotating-circular-arrow.png" alt="rotate icon"/>

{#if isModelDetailVisible}
<div id="modal" on:click={()=> isModelDetailVisible = false}>
    <div id="model-detail">
        <span id="close-btn" on:click={()=>isModelDetailVisible = false}>
        <svg style="width:100%" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 475.2 475.2"><path d="M405.6 69.6C360.7 24.7 301.1 0 237.6 0s-123.1 24.7-168 69.6S0 174.1 0 237.6s24.7 123.1 69.6 168 104.5 69.6 168 69.6 123.1-24.7 168-69.6 69.6-104.5 69.6-168-24.7-123.1-69.6-168zm-19.1 316.9c-39.8 39.8-92.7 61.7-148.9 61.7s-109.1-21.9-148.9-61.7c-82.1-82.1-82.1-215.7 0-297.8C128.5 48.9 181.4 27 237.6 27s109.1 21.9 148.9 61.7c82.1 82.1 82.1 215.7 0 297.8z"/><path d="M342.3 132.9c-5.3-5.3-13.8-5.3-19.1 0l-85.6 85.6-85.6-85.6c-5.3-5.3-13.8-5.3-19.1 0-5.3 5.3-5.3 13.8 0 19.1l85.6 85.6-85.6 85.6c-5.3 5.3-5.3 13.8 0 19.1 2.6 2.6 6.1 4 9.5 4s6.9-1.3 9.5-4l85.6-85.6 85.6 85.6c2.6 2.6 6.1 4 9.5 4 3.5 0 6.9-1.3 9.5-4 5.3-5.3 5.3-13.8 0-19.1l-85.4-85.6 85.6-85.6c5.3-5.3 5.3-13.8 0-19.1z"/></svg> 
        </span>
        <h2>Single Chair Sofa</h2>
        <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ratione iure illum mollitia dolorem, aliquam unde. Unde a, ex similique quis eaque est libero ut autem, blanditiis consequatur dicta! Amet, recusandae.</p>
    </div>
    
</div>
{/if}

<style>
    #rotate-icon {
        display:block;
        position:fixed;
        bottom: 20%;
        left: 50%;
        transform: translateX(-50%);
        width: 15%;
        z-index: 2;
    }

    #modal {
        display:block;
        position:fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.7);
        z-index: 2;
    }

    #model-detail{
        display:block;
        position: fixed;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        width: 80%;
        height: auto;
        border-radius: 0.25rem;
        background-color: #FFF;
        z-index:3;
        padding: 0.5rem;
    }

    #close-btn {
        position: absolute;
        right: 2%;
        top: 2%;
        width:7.5%;
    }
</style>